.container-node .btn--menu:after {\n  transform: translateY(-5px);\n}\n\n.container-node .btn--menu:before {\n  transform: translateY(5px);\n}\n\n.container-node .btn--share {\n  background: #f39c12;\n}\n\n.container-node .btn--star {\n  background: #2ecc71;\n}\n\n.container-node .btn--comment {\n  background: #16a085;\n}\n\n.container-node .actions-menu {\n  position: fixed;\n  width: 60px;\n  height: 60px;\n  right: 30px;\n  bottom: 70px;\n}\n\n.container-node .actions-menu .btn {\n  position: absolute;\n  top: 8px;\n  left: 8px;\n  opacity: 0;\n  transition: top 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275), left 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 200ms;\n}\n\n.container-node .actions-menu .btn--menu {\n  position: absolute;\n  opacity: 1;\n  top: 0;\n  left: 0;\n  z-index: 0;\n}\n\n.container-node .btn--menu, .container-node #actionMenuButton:checked + .actions-menu > .btn {\n  opacity: 1;\n  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);\n}\n\n.container-node #actionMenuButton:checked + .actions-menu > .btn--menu:after {\n  transform: rotate(45deg);\n}\n\n.container-node #actionMenuButton:checked + .actions-menu > .btn--menu:before {\n  transform: rotate(-45deg);\n}\n\n.container-node #actionMenuButton:checked + .actions-menu > .btn--share {\n  top: -70px;\n}\n\n.container-node #actionMenuButton:checked + .actions-menu > .btn--star {\n  top: -50px;\n  left: -50px;\n}\n\n.container-node #actionMenuButton:checked + .actions-menu > .btn--comment {\n  left: -70px;\n}\n\n@media only screen and (min-width: 992px) {\n  ion-card {\n    right: -7%;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */";